<template>
  <v-container
    id="data-tables"
    class="fill-height align-start"
    fluid
    tag="section"
  >
    <v-container
      fluid
    >
      <v-card color="basil">
        <!--        <v-card-title class="text-center justify-center py-6">-->
        <!--          <h1 class="font-weight-bold display-3 black&#45;&#45;text">-->
        <!--            {{ $t('菜品出库统计') }}-->
        <!--          </h1>-->
        <!--        </v-card-title>-->

        <ik-data-table
          ref="statisticTable"
          :model="model"
          :use-action="false"
          :use-select="false"
          icon="mdi-calculator-variant"
          :entity-name="$t('菜品出库统计')"
          :filter="filter"
        >
          <template v-slot:filterLeft>
            <v-menu
              ref="menu"
              v-model="menu"
              :close-on-content-click="false"
              :return-value.sync="dates"
              offset-y
            >
              <template v-slot:activator="{ on }">
                <v-text-field
                  v-model="dates"
                  class="ml-auto"
                  hide-details
                  :label="$t('日期筛选')"
                  prepend-icon="mdi-calendar"
                  readonly
                  style="max-width: 250px;"
                  single-line
                  append-icon="mdi-close"
                  @click:append="dates=[]"
                  v-on="on"
                />
              </template>
              <v-date-picker
                v-model="dates"
                range
                locale="de"
              >
                <v-spacer/>
                <v-btn
                  text
                  color="primary"
                  @click="menu = false"
                >
                  {{ $t('Cancel') }}
                </v-btn>
                <v-btn
                  text
                  color="primary"
                  @click="$refs.menu.save(dates)"
                >
                  {{ $t('OK') }}
                </v-btn>
              </v-date-picker>
            </v-menu>
          </template>
        </ik-data-table>
      </v-card>
    </v-container>
  </v-container>
</template>

<script>

  import { IkDataTable } from 'metaflow-js'
  import DishDeliveryStatistics from '../../model/DishDeliveryStatistics'

  export default {
    components: {
      IkDataTable,
    },
    data: function () {
      return ({
        model: DishDeliveryStatistics,
        dates: [],
        menu: false,
      })
    },
    computed: {
      filter () {
        if (Date.parse(this.dates[0]) > Date.parse(this.dates[1])) {
          [this.dates[0], this.dates[1]] = [this.dates[1], this.dates[0]]
        }
        return { start: this.dates[0], end: this.dates[1] }
      },
    },
    mounted () {
    },
    methods: {},
  }
</script>
